on: absolute;\n    right: 0.6em;\n  }\n\n  .ui-label{\n    padding-right: 25px; // Based on a 16px icon to be positioned as chevron to the right\n  }\n}",".ui-tree-item{\n  display: flex;\n  padding: 0.6em;\n  align-items: center;\n  cursor: pointer;\n\n  > .collapse-glyph {\n    margin-right: 0.6em;\n  }\n}\n\n.tree-item-children{\n}",".ui-range{\n\n  height: 40px;\n  border-radius: 20px;\n  background: #f0f0f0;\n  background-size: contain;\n  position: relative;\n\n  .handle{\n    position: absolute;\n    width: 40px;\n    height: 40px;\n    border-radius: 100%;\n    background: Highlight;\n  }\n\n}",".ui-dock-view{\n\n  &, > .dock-side, > .main-side{\n    position: absolute;\n    top:0;\n    left:0;\n    right: 0;\n    bottom:0;\n  }\n\n  .dock-side, > .main-side {\n    overflow: auto;\n  }\n\n  &.side-left   > .dock-side{ right:  auto; }\n  &.side-top    > .dock-side{ bottom: auto; }\n  &.side-right  > .dock-side{ left:   auto; }\n  &.side-bottom > .dock-side{ top:    auto; }\n}",".ui-split-view{\n\n  &.side-left > .dock-side{   border-right-style: solid; border-right-width: 1px; }\n  &.side-top > .dock-side{    border-bottom-style: solid; border-bottom-width: 1px; }\n  &.side-right > .dock-side{  border-left-style: solid; border-left-width: 1px; }\n  &.side-bottom > .dock-side{ border-top-style: solid; border-top-width: 1px; }\n}",".ui-tab-view{\n  > .tabs{\n    > .items > .tab{\n      padding-left: 2em;\n      padding-right: 2em;\n    }\n  }\n\n}",".ui-tree-view{\n  position: absolute;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n}",".ui-toolbar-view{\n\n  > .toolbar{\n    display: flex;\n    justify-content: space-between;\n\n    > .items{\n      display: flex;\n      align-items: stretch;\n    }\n  }\n\n  > .content{\n    flex-grow: 1;\n    overflow: auto;\n  }\n\n  &.side-left, &.side-right{\n    > .toolbar{\n      flex-direction: column;\n\n      > .items{\n        flex-direction: column;\n      }\n    }\n  }\n\n}",".ui-menu-overlay {\n  min-width: 100px;\n  box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.3);\n  z-index: 10000;\n}",".ui-suggestions-overlay{\n  background-color: Menu;\n\n  .ui-clickable, .ui-loader{\n    padding: 0.6em;\n  }\n}",".ui-overlay{\n  position: fixed;\n  z-index: 10000;\n}"],"sourceRoot":""}]);
